import styled from 'styled-components'
import { variables } from '../../styles/variables';


console.log("variables: ", variables)
export const Button = styled.button`
  border: none;
  width: ${props => props.small ? '35px' : '100%'};
  height: ${props => props.small ? '35px' : '100%'};
  background-color: rgba(${props => props.like ? variables.primary : variables.secondary }, .8);
  box-sizing: border-box;
  border: 2px solid ${props => props.like ? variables.primary : variables.secondary };
  padding:  ${props => props.small ? '2px' : '15px'};
  &:hover {
    border: 2px solid white;
  }
  .like {
    width: ${props => props.small ? '20px' : 'auto'};
    height: ${props => props.small ? '20px' : 'auto'};
    transform: rotate(${props => props.like ? '0deg' : '180deg' })
  }
`;



